export default {
	props: {
		  item: {
		      type: Object,
		      // 对象或数组且一定会从一个工厂函数返回默认值
		      default: ()=>{
				  return {}
			  }
		    },
			index:Number
	},
	data() {
		return {};
	},
	methods: {
		open(){
			uni.navigateTo({
				url:"../../pages/topicDetail/topicDetail?item="+JSON.stringify(this.item)
			})
		}
	}
};
